<template>
  <div class="player-wrapper">
    <div class="default">Starting Lineup</div>
    <button
      @click="triggerModal('addPlayer')"
      ref="addPlayerButton"
      class="addPlayerButton"
    >
      Add Player
    </button>
    <ul>
      <li v-for="(player, index) in players" :key="index" class="player">
        <div class="name">{{ player.name }}</div>
        <span :class="player.role === 'player' ? '' : player.role" class="role">
          {{ player.role === 'player' ? '' : player.role }}
        </span>
        <div class="playerButtons">
          <button
            @click="
              triggerModal('editPlayer', { name: player.name, index: index, role: player.role })
            "
            class="playerButton"
            id="editPlayerButton"
          >
            edit
          </button>
          <button
            @click="
              triggerModal('removePlayer', { name: player.name, index: index })
            "
            class="playerButton"
            id="removePlayerButton"
          >
            delete
          </button>
        </div>
      </li>
    </ul>
  </div>
  <Modal ref="PlayerModal" @modalConfirmation="handleModalConfirmation" />
</template>

<script>
import { store } from "@/store";
import Modal from "./Modal.vue";

export default {
  name: "Players",

  components: {
    Modal,
  },

  data() {
    return {
      players: [],
      modalType: "",
      modalData: {},
      selectedPlayer: null,
    };
  },

  methods: {
    triggerModal(modalType, data = null) {
      if (modalType === "addPlayer" && this.players.length === 6) return;
      this.modalType = modalType;
      if (modalType === "removePlayer" || modalType === "editPlayer") {
        this.modalData = data;
        this.selectedPlayer = data;
      }
      this.$refs.PlayerModal.handleShowModal({
        type: this.modalType,
        data: this.modalData,
      });
    },
    handleModalConfirmation(playerData) {
      switch (this.modalType) {
        case "addPlayer":
          this.addPlayer(playerData.name, playerData.role);
          this.$refs.addPlayerButton.focus();
          break;
        case "removePlayer":
          this.removePlayer(this.selectedPlayer.index);
          break;
        case "editPlayer":
          this.editPlayer(
            this.selectedPlayer.index,
            playerData.name,
            playerData.role
          );
          break;
      }
    },
    addPlayer(player, role) {
      this.players.push({ name: player, role: role || "player" });
      store.addPlayer(player, role);
    },
    removePlayer(index) {
      this.players.splice(index, 1);
      store.removePlayer(index);
    },
    editPlayer(index, player, role) {
      this.players[index].name = player;
      this.players[index].role = role || "player";
      store.editPlayer(index, player, role);
    },
  },
};
</script>

